    <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoMarker">
        Name: {{infoContent.name}}<br>
        <!-- IP: {{infoContent.ip}}<br> -->
        <p v-if="infoContent.uptime=='0:00:00'">Status: Down <br> Uptime: 0:00:00 Hours</p>
        <p v-else>Status: Up <br> Uptime: {{ infoContent.uptime }} Hours</p>
         <a @click="zoomMap(infoWindowPos,'marker')">Zoom</a> || <a data-toggle="modal" data-target="#Graph" @click="getCPU(infoContent.id)">Graph</a> || <a @click="backToMap()">BackToMap</a> <!--|| <a data-toggle="modal" data-target="#myModal">History</a> -->
    </gmap-info-window> 
    <gmap-marker
      :key="index"
      v-for="(m, index) in filterData"
      :title="m.name"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      :icon="image.url"
      @click="toggleInfoWindow(m,index)"
    ></gmap-marker>

    <gmap-marker
      :key="index"
      v-for="(m, index) in markers"
      :title="m.name"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      :icon="image1.url"
      @click="toggleInfoWindow(m,index)">
      </gmap-marker>

    <gmap-info-window  :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false" v-if="infoPolyline">
        From: {{ infoContent.A }} <br>
        Port: {{ infoContent.port_A }} <br>
        --------------------------------------------- <br>
        <p>To: {{ infoContent.B }} <br>
        Port: {{ infoContent.port_B }}</p>
        <a @click="zoomMap(infoWindowPos,'line')">Zoom</a> || <a data-toggle="modal" data-target="#History" @click="loadHistory('All Time')">History</a> || 
        <a data-toggle="modal" data-target="#Problem"></a> || <a data-toggle="modal" data-target="#Graph">Graph</a>
        || <a @click="routeStreetView">Street View</a> || <a @click="backToMap()">BackToMap</a>
    </gmap-info-window> 
    <gmap-polyline
      v-for="(pl,index) in connects" 
      :key="index" 
