                <button type="button" class="btn btn-info" @click="loadHistory('All Time')">All Time</button> --- 
                <input type="date" name="bday" v-model="fDate"> - <input type="date" name="bday" v-model="tDate">
                <button type="button" class="btn btn-primary" @click="filterHistory2()">Search</button> <hr>
              <div v-for="(h,index) in history" :key="index">
                <h4>{{ h.broken_time }} - Distance: {{ h.distance }} m - 
                  <font v-if="h.status_broken==0" color="green"> - {{ h.timestamp }} </font>
                  <font v-if="h.status_broken==1" color="red"></font>
                  <font v-if="h.status_broken==2" color="Orange"></font>
                  <font v-if="h.status_broken==1">
                    <label class="checkbox-inline"><input @click="chkBroken=2" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <label class="checkbox-inline"><input @click="chkBroken=0" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <button type="submit" class="btn btn-primary" @click="checkBroken(index)"></button>
                  </font>
                  <font v-if="h.status_broken==2">
                    <label class="checkbox-inline"><input @click="chkBroken=2" type="radio" name="chkbroken" id="chkbroken" disabled></label> 
                    <label class="checkbox-inline"><input @click="chkBroken=0" type="radio" name="chkbroken" id="chkbroken"></label> 
                    <button type="submit" class="btn btn-primary" @click="checkBroken(index)"></button>
                  </font>
                </h4>              
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" @click="downloadHistory(history)">Export History</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>
      
      <div class="modal" id="Problem" tabindex="-1" role="dialog" aria-labelledby="Problem" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="Problem"></h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <h3> <input v-model="distance" type="text" placeholder=""/>  <button type="button" class="btn btn-primary" data-dismiss="modal" @click="saveHistory">Add</button></h3>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>

      <modal v-show="showModal" @close="showModal = false">
        <h3 slot="header">
          {{ infoContent.name }}
        </h3>
        <h3 slot="body">
          <button @click="filterChart(0)">Day</button> <button @click="filterChart(1)">Month</button> <button @click="filterChart(2)">Year</button>
          <card-line1-chart-example class="chart-wrapper px-3" style="height:300; width:320;" :chart-data="datacollection"/>
        </h3>
      </modal>

      <!-- <div class="modal" id="Panorama" tabindex="-1" role="dialog" aria-labelledby="Panorama" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h2 class="modal-title" id="Panorama">Street View</h2>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <gmap-street-view-panorama id="panorama"
                class="pano"
                style="width: 100%; height: 400px;"
                :position="mousePos"
                :pov="{
                  heading: 0,
                  pitch: 0
                }"
                :motionTracking="true"
                :visible="panoOpen"
                @status_changed="updatePano"
                :visible_changed="updatePano">
              </gmap-street-view-panorama>
              <button @click="panoOpen=true">panoOpen</button> <button @click="panoOpen=false">panoClose</button>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="panoOpen=false">Close</button>
            </div>
          </div>
        </div>
      </div> -->
    </div>
</template>

<script>
import CardLine1ChartExample from './dashboard/CardLine1ChartExample'
import CardLine2ChartExample from './dashboard/CardLine2ChartExample'
import CardLine3ChartExample from './dashboard/CardLine3ChartExample'
import CardBarChartExample from './dashboard/CardBarChartExample'
import MainChartExample from './dashboard/MainChartExample'
import SocialBoxChartExample from './dashboard/SocialBoxChartExample'
import CalloutChartExample from './dashboard/CalloutChartExample'
import { Callout } from '../components/'
import funcJS from '../function.js'
import modal from './modal'

import pdfMake from 'pdfmake/build/pdfmake'
import pdfFonts from 'pdfmake/build/vfs_fonts'
pdfMake.vfs = pdfFonts.pdfMake.vfs
pdfMake.fonts = {
  THSarabunNew: {
    normal: 'THSarabunNew.ttf',
    bold: 'THSarabunNew-Bold.ttf',
    italics: 'THSarabunNew-Italic.ttf',
    bolditalics: 'THSarabunNew-BoldItalic.ttf'
  },
  Roboto: {
    normal: 'Roboto-Regular.ttf',
    bold: 'Roboto-Medium.ttf',
    italics: 'Roboto-Italic.ttf',
    bolditalics: 'Roboto-MediumItalic.ttf'
  }
}

export default {
  name: 'dashboard',
  components: {
    modal,
    Callout,
    CardLine1ChartExample,
    CardLine2ChartExample,
    CardLine3ChartExample,
    CardBarChartExample,
    MainChartExample,
    SocialBoxChartExample,
    CalloutChartExample
  },
  data () {
    return {
      image: {
        // url: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png'
      },
      image1: {
        url: 'http://i.picresize.com/images/2018/02/19/Yb3u7.png'
      },
      goldStar: {
        path: 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0',
        strokeColor: 'gold',
        strokeWeight: 3
      },
      broke_distace: 105,
      percentile: ['0%', '100%'],
      zoom: 16,
      center: { lat: parseFloat(this.$route.query.lat), lng: parseFloat(this.$route.query.lng) },
      mousePos: { lat: 18.796861024696568, lng: 98.95122811930116 },
      infoContent: '',
      infoMarker: false,
      infoPolyline: false,
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      markers: [
        {
          position: {
            lat: 47.376332,
            lng: 8.547511
          },
          infoText: 'Marker 1'
        },
        {
          position: {
            lat: 47.374592,
            lng: 8.548867
          },
          infoText: 'Marker 2'
        },
        {
          position: {
            lat: 47.379592,
            lng: 8.549867
          },
          infoText: 'Marker 3'
        }
      ],
      plPath: [
        { lat: 18.7956453, lng: 98.952812 },
        { lat: 18.7982302, lng: 98.9491877 },
        { lat: 18.8004538, lng: 98.9504027 },
        { lat: 18.7932254, lng: 98.9562057 },
        { lat: 18.7956453, lng: 98.952812 }
      ],
      lineColor: 'RED',
      ppath: [
        {
          path: [
            { lat: 18.803528, lng: 98.952944 },
            { lat: 18.803289, lng: 98.952839 },
            { lat: 18.803622, lng: 98.951974 },
            { lat: 18.801703, lng: 98.951205 },
            { lat: 18.800288, lng: 98.951428 },
            { lat: 18.799764, lng: 98.951980 },
            { lat: 18.799236, lng: 98.951806 },
            { lat: 18.798836, lng: 98.951308 },
            { lat: 18.79685, lng: 98.951251 },
            { lat: 18.79674, lng: 98.951974 },
            { lat: 18.796134, lng: 98.951974 },
            { lat: 18.796137, lng: 98.951652 }
          ]
        },
        {
          path: [
            { lat: 18.7982302, lng: 98.9491877 },
            { lat: 18.7956453, lng: 98.952812 }
          ]
        },
        {
          path: [
            { lat: 18.8004538, lng: 98.9504027 }
            // { lat: 18.7982302, lng: 98.9491877 }
          ]
        },
        {
          path: [
            { lat: 18.7932254, lng: 98.9562057 },
            { lat: 18.7932254, lng: 98.9562057 }
          ]
        }
      ],
      plvisible: true,
      switchs: [],
      connects: [],
      search: '',
      columns: [
        {
          label: 'Name',
          field: 'name',
          filterable: false
        },
        {
          label: 'Status',
          field: 'status',
          type: 'string',
          html: false,
          filterable: false
        },
        {
          label: 'Uptime',
          field: 'uptime',
          type: 'number',
          html: false,
          filterable: false
        }
      ],
      isTableMarker: true,
      columnsLine: [
        {
          label: 'From',
          field: 'A',
          filterable: false
        },
        {
          label: 'To',
          field: 'B',
          filterable: false
        },
        {
          label: 'Status',
          field: 'linestatus',
          html: false,
          filterable: false
        }
      ],
      isTableLine: false,
      distance: '',
      history: [{ comment: '' }],
      dateHistory: 'All Time',
      fDate: '',
      tDate: '',
      myLocation: '',
      cpu_usage: [],
      datacollection: null,
      showModal: false,
      date_usage: []
    }
  },
  methods: {
    hellojs: funcJS.hello,
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    showBroken () {
      // console.log(this.connects)
      // this.goldStar.path = 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0'
      // this.goldStar.strokeColor = 'blue'
      // this.goldStar.strokeWeight = 3
      this.axios
        .get('https://fibermap.herokuapp.com/getbroken')
        .then(response => {
          this.broke_distace = response.data
          // console.log(this.broke_distace.length)
          for (var i = 0; i < this.broke_distace.length; i++) {
            for (var j = 0; j < this.connects.length; j++) {
              if (this.broke_distace[i].line_id === this.connects[j].id) {
                this.connects[j]['broke_distance'] = this.broke_distace[i].distance
                this.connects[j]['icon'] = {'path': 'M-0.1,0a0.1,0.1 0 1,0 0.2,0a0.1,0.1 0 1,0 -0.2,0 M-6.5,0a6.5,6.5 0 1,0 13,0a6.5,6.5 0 1,0 -13,0', 'strokeColor': 'gold', 'strokeWeight': 3}
              } else if (this.connects[j].broke_distance === undefined) {
                this.connects[j]['broke_distance'] = 0
                this.connects[j]['icon'] = {'path': '', 'strokeColor': 'gold', 'strokeWeight': 3}
              }
            }
          }
          this.calDistance()
        })
    },
    calDistance () {
      console.log(this.connects[0])
      var distance = 0
      for (var i = 0; i < this.connects[0].path.length - 1; i++) {
        distance = distance + this.getDistance(this.connects[0].path[i].lat, this.connects[0].path[i].lng, this.connects[0].path[i + 1].lat, this.connects[0].path[i + 1].lng)
      }
      this.percentile = ((this.connects[0].broke_distance / 1000) * 100) / distance
      this.percentile = this.percentile.toString() + '%'
      this.connects[0]['broken_percentile'] = this.percentile
      // console.log(this.connects[x])
      // console.log(this.connects[1].broke_distance)
    },
    getDistance (slat, slng, flat, flng) {
      var R = 6371
      var dLat = (flat - slat) * (Math.PI / 180)
      var dLng = (flng - slng) * (Math.PI / 180)
      var a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(slat * (Math.PI / 180)) * Math.cos(flat * (Math.PI / 180)) *
        Math.sin(dLng / 2) * Math.sin(dLng / 2)
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
      var d = R * c
      return d
      // console.log(d)
    },
    checkBroken (index) {
      // console.log(this.chkBroken)
      // console.log(this.history[index].status_broken)
      // console.log(this.history[index].distance)
      this.axios.get('https://fibermap.herokuapp.com/updatehistory', {
        params: {
          lineid: this.history[index].line_id,
          distance: this.history[index].distance,
          status_broken: this.chkBroken
        }
      })
        .then(response => {
          this.loadHistory('All Time')
        })
    },
    saveHistory () {
      this.axios.get('https://fibermap.herokuapp.com/savehistory', {
        params: {
          id: this.infoContent.id,
          A: this.infoContent.A,
          B: this.infoContent.B,
          distance: this.distance
        }
      })
      window.alert('')
      this.distance = ''
    },
    loadHistory (dateString) {
      this.dateHistory = dateString
      this.axios
        .get('https://fibermap.herokuapp.com/loadhistory', {
          params: {
            id: this.infoContent.id
          }
        })
        .then(response => {
          this.history = response.data
          console.log(response.data)
        })
    },
    filterHistory (value, dateString) {
      this.dateHistory = dateString
      this.axios
        .get('https://fibermap.herokuapp.com/filterhistory', {
          params: {
            id: this.infoContent.id,
            date: value
          }
        })
        .then(response => {
          this.history = response.data
        })
    },
    filterHistory2 () {
      this.dateHistory = this.fDate + ' - ' + this.tDate
      console.log(this.dateHistory)
      this.axios
        .get('https://fibermap.herokuapp.com/filterhistory2', {
          params: {
            id: this.infoContent.id,
            fromDate: this.fDate,
            toDate: this.tDate
          }
        })
        .then(response => {
          this.history = response.data
        })
    },
    downloadHistory (history) {
      var broken
      var distance
      var status
      var fixed
      for (var i = 0; i < history.length; i++) {
        if (broken === undefined) {
          broken = history[i].broken_time.toString()
          distance = history[i].distance.toString() + ' m'
          if (parseInt(history[i].status_broken) === 0) {
            status = ''
            fixed = history[i].timestamp.toString()
          } else if (parseInt(history[i].status_broken) === 1) {
            status = ''
            fixed = '-'
          } else if (parseInt(history[i].status_broken) === 2) {
            status = ''
            fixed = '-'
          }
        } else {
          broken = broken + '\n' + history[i].broken_time.toString()
          distance = distance + '\n' + history[i].distance.toString() + ' m'
          if (parseInt(history[i].status_broken) === 0) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + history[i].timestamp.toString()
          } else if (parseInt(history[i].status_broken) === 1) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + '-'
          } else if (parseInt(history[i].status_broken) === 2) {
            status = status + '\n' + ''
            fixed = fixed + '\n' + '-'
          }
        }
      }
      var docDefinition = {
        content: [
          {text: 'History [' + this.dateHistory + ']', style: 'header', fontSize: 30},
          history[0].comment,
          {
            layout: 'lightHorizontalLines', // optional
            table: {
              // headers are automatically repeated if the table spans over multiple pages
              // you can declare how many rows should be treated as headers
              headerRows: 1,
              widths: [ 'auto', 'auto', 'auto', 'auto' ],

              body: [
                [ '', '', '', '' ],
                [ broken, distance, status, fixed ]
              ]
            }
          }
        ],
        defaultStyle: {
          font: 'THSarabunNew',
          fontSize: 18
        }
      }
      pdfMake.createPdf(docDefinition).download(history[0].comment + ' [' + this.dateHistory + '].pdf')
    },
    showLayer (value, e) {
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'coretocore') {
        this.switchs = []
        this.connects = []
        this.axios
          .get('https://fibermap.herokuapp.com/showlayerRP')
          .then(response => {
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
            this.showBroken()
          })
      }
      if (value === 'zone') {
        this.switchs = []
        this.connects = []
        this.axios
          .get('https://fibermap.herokuapp.com/showzoneRP', {
            params: {
              zone: e
            }
          })
          .then(response => {
            this.center = response.data[0].switch[0].position
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
            this.showBroken()
          })
      }
    },
    backToMap () {
      location.href = 'http://fibermap.github.io/#/map'
    },
    zoomMap (pos, obj) {
      if (obj === 'marker') {
        this.center = pos
        this.zoom = 18
      } else if (obj === 'line') {
        this.center = pos
        this.zoom = 17
      }
    },
    updateZoom (zoom) {
      // console.log(zoom)
      this.zoom = zoom
    },
    updateCenter (center) {
      this.center = {
        lat: center.lat(),
        lng: center.lng()
      }
    },
    routeStreetView () {
      // location.href = 'http://maps.google.com/maps?q=&layer=c&cbll=' + this.mousePos.lat + ',' + this.mousePos.lng
      window.open('http://maps.google.com/maps?q=&layer=c&cbll=' + this.mousePos.lat + ',' + this.mousePos.lng, '_blank')
    },
    posMouse (pos) {
      this.mousePos = {
        lat: pos.latLng.lat(),
        lng: pos.latLng.lng()
      }
    },
    showTable (value) {
      // console.log(value)
      if (value === 'switch') {
        this.isTableMarker = true
        this.isTableLine = false
      } else if (value === 'line') {
        this.isTableMarker = false
        this.isTableLine = true
      }
    },
    showByStatus (value, e) {
      // console.log(value)
      // console.log(e)
      if (value === 'all') {
        this.getSwitch()
        this.getConnect()
      }
      if (value === 'updown') {
        if (e === 1) {
          this.switchs = []
          this.connects = []
        }
        this.axios
          .get('https://fibermap.herokuapp.com/showbystatusRP', {
            params: {
              status: e
            }
          })
          .then(response => {
            // console.log(response.data[0].switch);
            this.switchs = response.data[0].switch
            this.connects = response.data[0].connect
          })
      }
    },
    toggleInfoWindow (marker, idx) {
      this.infoOptions.pixelOffset.height = -35
      this.infoWindowPos = marker.position
      this.infoContent = marker
      this.center = marker.position
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoMarker = true
      this.infoPolyline = false
    },
    toggleInfoWindow2 (line, idx, e) {
      this.infoOptions.pixelOffset.height = 0
      if (e === 1) {
        var midLat = this.mousePos.lat
        var midLng = this.mousePos.lng
      } else {
        if ((line.path.length % 2) === 0) {
          midLat = (line.path[line.path.length / 2].lat)
          midLng = (line.path[line.path.length / 2].lng)
        } else {
          midLat = (line.path[line.path.length / 2 - 0.5].lat + line.path[line.path.length / 2 + 0.5].lat) / 2
          midLng = (line.path[line.path.length / 2 - 0.5].lng + line.path[line.path.length / 2 + 0.5].lng) / 2
        }
        this.center = { lat: midLat, lng: midLng }
      }
      console.log(line.path.length)
      this.infoWindowPos = { lat: midLat, lng: midLng }
      this.infoContent = line
      this.infoWinOpen = true
      this.currentMidx = idx
      this.infoPolyline = true
      this.infoMarker = false
    },
    getMapToRealpath () {
      this.axios
        .get('https://fibermap.herokuapp.com/realpathbyid', {
          params: {
            lineid: this.$route.query.lineid
          }
        })
        .then(response => {
          // console.log(response.data[0].switch)
          this.switchs = response.data[0].switch
          this.connects = response.data[0].connect
          this.showBroken()
        })
    },
    getCPU (value) {
      this.cpu_usage = []
      this.date_usage = []
      this.axios
        .get('https://fibermap.herokuapp.com/getcpu', {
          params: {
            device_id: value
          }
        })
        .then(response => {
          console.log(response.data)
          this.cpu_usage = response.data
          this.date_usage = response.data
          this.fillData(this.cpu_usage.day.cpu, this.date_usage.day.time)
          this.showModal = true
          console.log(this.cpu_usage)
          console.log(this.date_usage)
        })
    },
    fillData (val, date) {
      this.datacollection = {
        labels: date,
        datasets: [
          {
            label: 'CPU Usage (%)',
            backgroundColor: '#f87979',
            data: val
          }
        ]
      }
    },
    filterChart (val) {
      if (val === 0) {
        this.fillData(this.cpu_usage.day.cpu, this.date_usage.day.time)
      } else if (val === 1) {
        this.fillData(this.cpu_usage.month.cpu, this.date_usage.month.time)
      } else if (val === 2) {
        this.fillData(this.cpu_usage.year.cpu, this.date_usage.year.time)
      }
    }
  },
  created: function () {
    this.getMapToRealpath()
    // var eiei = this.$route.query.lineid
    // console.log(parseInt(eiei))
    // console.log(this.$route)
  },
  computed: {
    filterData () {
      return this.switchs.filter(sw => {
        return sw.name.match(this.search)
      })
    }
  },
  mounted: function () {
    if (navigator.geolocation) {
      var self = this
      navigator.geolocation.getCurrentPosition(function (position) {
        self.myLocation = position.coords
        self.markers[0].position.lat = position.coords.latitude
        self.markers[0].position.lng = position.coords.longitude
        // console.log(position.coords.latitude)
        // console.log(position.coords.longitude)
        // console.log(self.myLocation)
      })
    }
  }
}
</script>



