<script>
import CardLine1ChartExample from './dashboard/CardLine1ChartExample'
import CardLine2ChartExample from './dashboard/CardLine2ChartExample'
import CardLine3ChartExample from './dashboard/CardLine3ChartExample'
import CardBarChartExample from './dashboard/CardBarChartExample'
import MainChartExample from './dashboard/MainChartExample'
import SocialBoxChartExample from './dashboard/SocialBoxChartExample'
import CalloutChartExample from './dashboard/CalloutChartExample'
import { Callout } from '../components/'
import modal from './modal'

export default {
  name: 'dashboard',
  components: {
    modal,
    Callout,
    CardLine1ChartExample,
    CardLine2ChartExample,
    CardLine3ChartExample,
    CardBarChartExample,
    MainChartExample,
    SocialBoxChartExample,
    CalloutChartExample
  },
  data: function () {
    return {
      cpuData1: [29, 58, 65, 22, 11, 100, 23],
      cpuData2: [20, 45, 22, 51, 29, 58, 65],
      datasets1: [
        {
          label: 'My First dataset',
          backgroundColor: '#20a8d8',
          borderColor: 'rgba(255,255,255,.55)',
          data: [20, 45, 22, 51, 29, 58, 65]
        }
      ],
      datacollection: null,
      cpu_usage: [],
      date_usage: [],
      showModal: false
    }
  },
  methods: {
    fillData () {
      this.datacollection = {
        labels: this.date_usage,
        datasets: [
          {
            label: 'CPU Usage (%)',
            backgroundColor: '#f87979',
            data: this.cpu_usage
          }
        ]
      }
    },
    changeData (e) {
      if (e === 1) {
        this.datacollection = {
          labels: this.date_usage,
          datasets: [
            {
              label: 'Data One',
              backgroundColor: '#f87979',
              data: this.cpu_usage
            }
          ]
        }
      }
      if (e === 2) {
        this.datacollection = {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
          datasets: [
            {
              label: 'Data One',
              backgroundColor: '#f87979',
              data: this.cpuData2
            }
          ]
        }
      }
      console.log(this.datasets1.data)
    },
    viewCPU () {
      console.log(this.$route.query.id)
      this.axios
        .get('https://fibermap.herokuapp.com/getcpu', {
          params: {
            device_id: this.$route.query.id
          }
        })
        .then(response => {
          console.log(response.data[0].cpu)
          for (let i = 0; i < response.data.length; i++) {
            this.cpu_usage.push(response.data[response.data.length - i - 1].avg_cpu)
            this.date_usage.push(response.data[response.data.length - i - 1].time)
          }
          this.fillData()
          console.log(this.cpu_usage)
          console.log(this.date_usage)
        })
    }
  },
  created () {
    // this.viewCPU()
  },
  mounted () {
    this.viewCPU()
    // this.fillData()
    // this.changeData()
  }
}
</script>


